body{
  width: 100%;
  height: auto;
  align-self: center;
}
.App {
  text-align: center;
}

.Main-app{
  width:30%;
  margin-left:auto;
  margin-right: auto;
  border-radius: 2%;
  background: cyan;
  align-items: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
